/* Updated print styles for centering content and increasing size */

@media print {
  .container {
    width: 100%;
    text-align: center;
    margin: 0 auto;
    padding: 20px;
  }

  .left,
  .right {
    display: inline-block;
    width: 45%;
    vertical-align: top;
    text-align: left;
    margin-bottom: 10px;
  }

  .left {
    text-align: right;
    padding-right: 10px;
  }

  .right {
    text-align: left;
    padding-left: 10px;
  }

  body,
  html {
    height: 100%;
    margin: 0;
    font-size: 24px;
  }

  @page {
    size: A4;
    margin: 1in;
  }
}

/* responsive  */

@media only screen and (max-width: 480px) {
  .form-group {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    padding: 5px;
    margin-right: 0px;
    margin-left: 0px;
  }

  .submitted-forms {
    margin-top: 50px;
    margin-right: 10px;
    margin-left: 10px;
  }
}
